import React from 'react';
import './css/dashboard.css';
import Serach from '../search.svg';
import Notification from '../notification.svg';
import {MemoryRouter, Routes, Route, NavLink,} from 'react-router-dom';
import Storage from "./Storage";
import Sales from "./Sales";
import Staff from "./Staff";
import Chat from "./Chat";
import Settings from "./Settings";

class Dashboard extends React.Component{
    render() {
        return(
            <div>
                <header>
                    <div className="upheader">
                        <form>
                            <img src={Serach} className="search_icon"/>
                            <input placeholder="Поиск" type="search"/>
                        </form>
                        <img src={Notification}/>
                    </div>
                    <div className="downheader">
                        <h1>Панель управления</h1>
                        <div>
                            <NavLink to="/dashboard/company" className={'category_inactive'} activeClassName="selected">Компания</NavLink>
                            <NavLink to="/dashboard/production" className={'category_inactive'} activeClassName="selected">Производство</NavLink>
                            <NavLink to="/dashboard/shops" className={'category_inactive'} activeClassName="selected">Магазины</NavLink>
                        </div>
                    </div>
                </header>
                <Routes>
                    <Route path="/dashboard/company" element={<h1>123</h1>}/>
                    <Route path="/dashboard/production" element={<Storage/>}/>
                    <Route path="/dashboard/shops" element={<Sales/>}/>
                </Routes>
            </div>
    )}}

export default Dashboard;
