import React from 'react';
import './App.css';
import {BrowserRouter, Routes, Route, NavLink,} from 'react-router-dom';

import Dashboard from './screens/Dashboard';
import Storage from './screens/Storage';
import Sales from './screens/Sales';
import Staff from './screens/Staff';
import Chat from './screens/Chat';
import Settings from './screens/Settings';

import logo_green from './logo_green.svg';
import dashboard_icon from './dashboard_icon.svg';
import storage_icon from './storage_icon.svg';
import sales_icon from './sales_icon.svg';
import staff_icon from './staff_icon.svg';
import chat_icon from './chat_icon.svg';
import settings_icon from './settings_icon.svg';
import upgrade from './upgrade.svg';
import download_app from './download.svg';

function App() {
    return (
        <BrowserRouter>
            <div className="main_body">
                <div className="left_panel">
                    <img src={logo_green} height="48px"/>
                    <br/>
                    <button className="learning">Обучение</button>
                    <div className="menu">
                        <img src={dashboard_icon} height="20px"/><NavLink to="/dashboard" className={'link_inactive'} activeClassName="selected">Панель<br/>управления</NavLink>
                        <img src={storage_icon} height="20px"/><NavLink to="/storage" className={'link_inactive'} activeClassName="selected">Склад</NavLink>
                        <img src={sales_icon} height="20px"/><NavLink to="/sales" className={'link_inactive'}>Продажи</NavLink>
                        <img src={staff_icon} height="20px"/><NavLink to="/staff" className={'link_inactive'}>Персонал</NavLink>
                        <img src={chat_icon} height="20px"/><NavLink to="/chat" className={'link_inactive'}>Чат</NavLink>
                        <img src={settings_icon} height="20px"/><NavLink to="/settings" className={'link_inactive'}>Настройки</NavLink>
                    </div>
                    <div className="upgrade">
                        <img src={upgrade}/>
                        <button>Обновиться</button>
                    </div>
                </div>
                <div className="main_screen">
                    <Routes>
                        <Route path="/dashboard" element={<Dashboard/>}/>
                        <Route path="/storage" element={<Storage/>}/>
                        <Route path="/sales" element={<Sales/>}/>
                        <Route path="/staff" element={<Staff/>}/>
                        <Route path="/chat" element={<Chat/>}/>
                        <Route path="/settings" element={<Settings/>}/>
                    </Routes>
                    </div>
                <div className="right_panel">
                    <img src={download_app}/>
                </div>
            </div>
        </BrowserRouter>
  );}

export default App;
